@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Oswald";
  src: url("../fonts/oswald/oswaldregular.woff2") format("woff2"), url("../fonts/oswald/oswaldregular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Lato";
  src: url("../fonts/lato/latoregular.woff2") format("woff2"), url("../fonts/lato/latoregular.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
  min-height: 100vh;
}

body {
  min-height: 100vh;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  font-family: "Lato", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: cover;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
}

.list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  display: inline-block;
  width: 100%;
  padding: 10px;
  font-size: 16px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  text-align: center;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button:hover {
  background-color: #5eaa2f;
}
.button:focus, .button:focus-visible {
  color: rgba(255, 255, 255, 0.7);
  background-color: #6cc236;
  outline: none;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled, .button--disabled {
  color: #444444;
  background-color: #cdcdcd;
  opacity: 0.3;
  pointer-events: none;
}

.title {
  margin: 0;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", "Arial", sans-serif;
}
@media (min-width: 768px) {
  .title {
    font-size: 60px;
    line-height: 60px;
  }
}

.title-lines {
  position: relative;
  margin: 0 -20px;
  overflow: hidden;
  font-size: 24px;
  line-height: 26px;
}
@media (min-width: 768px) {
  .title-lines {
    margin: 0 -30px;
    padding-left: 30px;
    font-size: 36px;
    line-height: 36px;
  }
}
@media (min-width: 1440px) {
  .title-lines {
    margin: 0 -110px;
    padding-left: 70px;
  }
}
.title-lines::before, .title-lines::after {
  content: "";
  position: absolute;
  top: 28%;
  display: inline-block;
  width: 100%;
  height: 2px;
  vertical-align: middle;
  background-color: #68b738;
}
@media (min-width: 768px) {
  .title-lines::before, .title-lines::after {
    top: 25px;
  }
}
.title-lines::before {
  display: none;
  margin-left: -100%;
}
@media (min-width: 768px) {
  .title-lines::before {
    display: inline-block;
  }
}
.title-lines::after {
  margin-right: -100%;
}

.title-lines span {
  display: inline-block;
  max-width: 222px;
  padding: 0 20px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .title-lines span {
    max-width: 100%;
    padding: 0 40px;
  }
}

.container {
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 20px;
}
@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
@media (min-width: 1440px) {
  .container {
    padding: 0;
  }
}

@media (min-width: 1440px) {
  .header {
    position: relative;
    padding-bottom: 53px;
  }
}

@media (max-width: 767px) {
  .header__container {
    padding: 0;
  }
}

.header__navigation {
  padding: 13px 0 14px;
}
@media (min-width: 768px) {
  .header__navigation {
    padding: 25px 0 14px;
  }
}
@media (min-width: 1440px) {
  .header__navigation {
    padding: 55px 0 28px;
  }
}

@media (min-width: 1440px) {
  .header--home:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 50%;
    z-index: -1;
    background-color: rgba(104, 183, 56, 0.85);
    background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-background-desktop.png");
    background-repeat: no-repeat;
    background-position: left;
    background-size: cover;
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .header--home:after {
    background-image: linear-gradient(to top, rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../img/cat-background-desktop@2x.png");
  }
}

.navigation {
  position: relative;
  width: 100%;
  min-height: 65px;
}
@media (min-width: 768px) {
  .navigation {
    display: grid;
    align-items: center;
    grid-template-columns: repeat(2, auto);
  }
}

.navigation__logo {
  display: block;
  width: 190px;
  height: 38px;
  margin-left: 20px;
}
@media (min-width: 768px) {
  .navigation__logo {
    width: 174px;
    height: 50px;
    margin-left: 0;
  }
}
@media (min-width: 1440px) {
  .navigation__logo {
    width: 202px;
    height: 59px;
  }
}
.navigation__logo:hover {
  opacity: 0.8;
}
.navigation__logo:focus, .navigation__logo:focus-visible, .navigation__logo:active {
  outline: none;
  opacity: 0.6;
}

.navigation__logo img {
  display: block;
  width: 100%;
  height: 100%;
}

.navigation__list {
  position: absolute;
  top: 100%;
  right: -20px;
  left: -20px;
  z-index: 2;
  display: none;
  flex-direction: column;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .navigation__list {
    position: static;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 4px;
    gap: 34px;
  }
}
@media (min-width: 1440px) {
  .navigation__list {
    margin-right: 0;
    background-color: transparent;
  }
}

.navigation__item {
  padding: 22px 10px 20px;
  border-top: 1px solid #f2f2f2;
}
@media (min-width: 768px) {
  .navigation__item {
    padding: 0;
    border: none;
  }
}

.navigation__link {
  font-weight: 400;
  font-size: 20px;
  line-height: 20px;
  font-family: "Oswald", "Arial", sans-serif;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation__link {
    padding: 8px 0;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1440px) {
  .navigation__link {
    padding: 6px 0;
    font-size: 20px;
    line-height: 30px;
  }
}
.navigation__link:hover {
  opacity: 0.6;
}
.navigation__link:focus, .navigation__link:focus-visible, .navigation__link:active {
  outline: none;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .navigation__link--current {
    border-bottom: 2px solid #68b738;
  }
}

@media (min-width: 1440px) {
  .header--home .navigation__link--current {
    border-bottom: 2px solid #ffffff;
  }
}

@media (min-width: 1440px) {
  .header--home .navigation__link {
    color: #ffffff;
  }
}

.navigation.is-open .navigation__list {
  display: flex;
}

.navigation__toggle {
  position: absolute;
  top: 22px;
  right: 20px;
  width: 24px;
  height: 18px;
  background-color: transparent;
  border: none;
  border-top: 2px solid #000000;
  cursor: pointer;
}
@media (min-width: 768px) {
  .navigation__toggle {
    display: none;
  }
}
.navigation__toggle::before, .navigation__toggle::after {
  content: "";
  position: absolute;
  left: 50%;
  width: 24px;
  height: 2px;
  border-top: 2px solid #000000;
  transform: translateX(-50%);
}
.navigation__toggle::before {
  top: 6px;
}
.navigation__toggle::after {
  bottom: 0;
}

.navigation.is-open .navigation__toggle {
  border-color: transparent;
}
.navigation.is-open .navigation__toggle::before {
  transform: translateX(-50%) rotate(45deg);
}
.navigation.is-open .navigation__toggle::after {
  bottom: 8px;
  transform: translateX(-50%) rotate(-45deg);
}

.no-js .navigation__logo {
  margin-bottom: 10px;
}

.no-js .navigation__list {
  position: static;
  display: block;
}

